import { Component, Inject, OnInit } from '@angular/core';
import { OrdenesDeCompraService } from '../../service/ordenes-de-compra.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AppAlertService } from 'app/shared/service/app/alert.service';
import { IArticulo } from 'app/modules/stock/articulos/models/articulo';
import { Observable, Subject, debounceTime, distinctUntilChanged, finalize, firstValueFrom, map, startWith, switchMap, takeUntil, takeWhile, tap, timer } from 'rxjs';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { MAT_DATE_FORMATS } from '@angular/material/core';
import { MatTableDataSource } from '@angular/material/table';
import { MY_DATE_FORMATS } from '@shared/constants/date-format';
import { IAlertConfig } from '@shared/models/alert';
import { EmpresaService } from '@shared/service/app/empresa.service';

@Component({
    selector: 'app-add-articulo-movimientos-internos',
    templateUrl: './add-articulo.component.html',
    styleUrls: ['./add-articulo.component.scss'],
    providers: [{ provide: MAT_DATE_FORMATS, useValue: MY_DATE_FORMATS }]
})
export class AddArticuloComponent implements OnInit {
    columns = ['articulo', 'codigointerno', 'unidadmedida', 'cantidad', 'acciones'];
    form: FormGroup;
    title: string;
    articulos: IArticulo[];
    unidades_de_medida: any[] = [];
    filteredArticulos: Observable<IArticulo[]>;
    public dataSource: MatTableDataSource<any> = new MatTableDataSource();
    showAlert = false;
    _alertConfig: IAlertConfig;
    color_primario: string;
    color_secundario: string;
    default_color: string = 'bg-primary';
    private _unsubscribeAll: Subject<any> = new Subject<any>();

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private _ordenesDeComnpraService: OrdenesDeCompraService,
        private _empresaService: EmpresaService,
        public alert: AppAlertService,
        public dialogRef: MatDialogRef<AddArticuloComponent>
    ) {
        this.title = 'Añadir articulo';
    }

    ngOnInit(): void {
        // Subscribe to empresa data
        this._empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe(empresa => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
            });

        // Load empresa data
        this._empresaService.getEmpresa();
        this.newForm();
        this.getArticulos();
        this.subscribeAlert();
    }

    // Alert
    subscribeAlert() {
        this.alert.emitConfig.subscribe({
            next: (config: IAlertConfig) => {
                this.showAlert = true;
                this._alertConfig = {
                    ...config,
                    appearance: 'border',
                };
                let time = 2;
                timer(100, 1500)
                    .pipe(
                        finalize(() => {
                            this.showAlert = false;
                            this._alertConfig = null;
                        }),
                        takeWhile(() => time > 0),
                        takeUntil(this._unsubscribeAll),
                        tap(() => (time -= 1))
                    )
                    .subscribe();
            },
        });
    }

    newForm() {
        this.form = new FormGroup({
            articulo: new FormControl((''), Validators.required),
            unidadmedida: new FormControl((''), Validators.required),
            cantidad: new FormControl((''), Validators.required),
            importe: new FormControl((''), Validators.required),
        });

        this.form.get('articulo').valueChanges.pipe(
            startWith(''),
            debounceTime(750),
            distinctUntilChanged(),
            switchMap((value) => this._ordenesDeComnpraService.getArticulos(value)),
        ).subscribe((v) => {
            this.filteredArticulos = v?.result;
        });
    }

    async getArticulos() {
        const articulos = await firstValueFrom(this._ordenesDeComnpraService.getArticulos());
        new Promise(async (resolve) => {
            this.articulos = articulos.list;
            resolve(true);
        });
    }

    getTextArticulo(articulo: IArticulo): string {
        return articulo ? `${articulo.codigo_interno} - ${articulo.descripcion}` : '';
    }

    async getUnidadesDeMedidaArticulos(idarticulo) {
        const unidadesDeMedida = await firstValueFrom(this._ordenesDeComnpraService.getUnidadesDeMedidaArticulos(idarticulo));
        new Promise(async (resolve) => {
            this.unidades_de_medida = unidadesDeMedida.list;
            resolve(true);
        })
    }

    /**
     * Esta funcion carga solamente las unidades de medida asociadas a un articulo en particular.
     * @param $event 
     */
    seleccionArticulo($event) {
        if ($event.idarticulo) {
            this.getUnidadesDeMedidaArticulos($event.idarticulo)
        }
    }

    /**
    * Funcion para limpiar campos
    */
    limpiarCasillero(input: string) {
        this.form.get(input).setValue('');
    }



    onSubmit() {
        if (!this.form.valid) {
            return this.alert.error('Revise Los Datos Ingresados');
        } else {
            let existeMovimiento = this.data.movimientos.find(movimiento => movimiento.articulo === this.form.get('articulo').value.idarticulo && movimiento.idunidadmedida === this.form.get('unidadmedida').value);
            if (existeMovimiento) {
                this.form.get('unidadmedida').setValue('');
                return this.alert.warning('Ya existe un movimiento de ese articulo con esa unidad de medida.')
            }

            let unidadmedida = this.unidades_de_medida.find(um => um.idunidadmedida === this.form.get('unidadmedida').value); //Traigo una UM que coincida con mi seleccion para poder utilizar su descripcion.
            let articulo = {
                articulo: this.form.get('articulo').value,
                unidad_medida: this.form.get('unidadmedida').value,
                cantidad: this.form.get('cantidad').value,
                importe: this.form.get('importe').value,
                unidadmedida_descripcion: unidadmedida.um.descripcion
            }
            this.onCloseMenu(articulo)
        }
    }

    onCloseMenu(articulo?) {
        this.dialogRef.close(articulo ? articulo : false)
    }
}